@use '../_abstracts' as *;

.sign {
    position: absolute;
    right: 25px;
    top: calc(50% - 4px);
    transform: translateY(-50%);

    &__left {
        display: inline-block;
        border-right: 2px solid color(darks);
        border-bottom: 2px solid color(darks);
        width: 8px; 
        height: 8px;
        transform: rotate(45deg);
        transition: .2s;
    }

    .sign-open {
        transform: rotate(225deg);
    }
}
