@use '../_abstracts/index' as *;

.sketch {
    position: absolute;
    background-image: url('/img/szkic.jpg');
    background-size: cover;
    background-position: top left;
    width: 100%;
    height: 300px;
    bottom: 0;
    opacity: 15%;
    mix-blend-mode: darken;
}

@include tablet {
    .sketch {
        width: 80%;
        height: 400px;
    }
}

@include ipad-pro {
    .sketch {
        width: 70%;
        height: 550px;
    }
}

@include laptop {
    .sketch {
        width: 60%;
        height: 550px;
    }
}