@use '../_abstracts/index' as *;

.status {
    position: absolute;
    bottom: 16px;
    padding: 0 $space-xs;
    color:color(darks);
    background-color: color(sands);
    transition: opacity .2s;
}

.show {
    background-color: color(lights);
}

.form {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: $space-xl 0 $space-xxxl 0;

    &__input {
        @include fluid-font (1vw, 16px, 16px, 16px);
        height: 40px;
        width: 90%;
        min-width: 250px;
        max-height: 250px;
        padding: $space-xxs;
        margin: $space-xxs 0;
        border: none;
        border-bottom: 2px solid color(golds);
        background-color: color(lights);
        font-family: inherit;
        resize: vertical;

        &:valid + span::before {
            content: '✓';
            position: relative;
            font-size: 18px;
            margin-left: $space-xs;
            color: color(greens);
        }

        &:focus {
            outline-color: color(golds);
            border-bottom: none;
            background-color: color(whites);
        }
    }

    &__title {
        text-align: center;
    }

    &__disclaimer {
        padding: $space-xs 0;
        line-height: 1.4;
        @include fluid-font (1vw, 12px, 13px, 12px);
        width: 100%;
    }

    &__label {
        cursor: pointer;
    }

    &__question {
        overflow: hidden;
    }

    &__submit {
        width: 170px;
        height: 45px;
        font-weight: bold;
        letter-spacing: 2px;
        margin: $space-xs 0;
        cursor: pointer;
        text-transform: uppercase;
        @include fluid-font (1vw, 16px, 18px, 18px);
        text-decoration: none;
        border: none;
        transition: background-color .3s, color .3s;
        color: color(lights);
        background-color: color(darks);

        &:focus {
            outline-color: color(golds);
        }

        &[value] {
            text-transform: uppercase;
            font-weight: bold;
            letter-spacing: 2px;
        }
    }
}

@media (hover: hover) {
    .form {
        &__submit {
            &:hover {
                background-color: color(lights);
                color: color(darks);
            }
        }
    }
}

@include tablet {
    .form {

        &__input {
            width: 100%;

            &:valid + span::before {
                position: absolute;
                padding-top: 10px;
                left: -31px;
                font-size: 20px;
            }
        }
    }
}

@include laptop {
    .form {
        &__input {
            &:valid + span::before {
                left: -45px;
                font-size: 22px;
            }
        }
    }
}
