@use '../_abstracts/index' as *;

.accordion {
    list-style: none;
    margin-bottom: $space-xl;

    &__item {
        border-top: 2px solid color(lightsands);
    }

    &__item:last-child {
        border-bottom: 2px solid color(lightsands);
    }

    &__title {
        position: relative;
        @include fluid-font (3vw, 20px, 22px, 22px);
        text-transform: uppercase;
        font-weight: normal;
        text-align: center;
        height: 70px;
        line-height: 70px;
        cursor: pointer;
        transition: background-color .3s;

        &:hover,&:focus {
            background-color: color(lightsands);
        }
    }

    &__option {
        text-align: left;
        font-weight: bold;
    }

    &__suboption {
        text-transform: lowercase;
        @include fluid-font (1vw, 14px, 16px, 16px);

        &--add {
            text-transform: none;
        }

        &--drinks {
            @include fluid-font (1vw, 16px, 20px, 18px);
            text-transform: none;
        }
    }

    &__container {
        position: relative;
        max-height: 0;
        overflow: hidden;
        transition: max-height .4s, background-color ease-out .3s;
        padding: 0 $space-xs;
    }
}

.active {
    height: auto;
    max-height: 5000px;
    background-color: color(lightsands);
}

.highlighted {
    background-color: color(lightsands);
}

@include tablet {
    .accordion {
        margin-bottom: $space-xxxl;
    }
}