@use '../_abstracts' as *;

.btn {
    position: relative;
    display: inline-block;
    width: 170px;
    height: 45px;
    line-height: 45px;
    text-transform: uppercase;
    text-align: center;
    font-weight: bold;
    letter-spacing: 2px;
    margin: $space-m 0;
    overflow: hidden;
    transition: background-color .3s, color .3s;
    z-index: 0;

    &__primary {
        background-color: color(darks);
        color: color(sands);
    }
}

@media (hover: hover) {
    .btn {
        &:hover {
            background-color: color(lights);
            color: color(darks);
        }
    }
}
